.manifesto-container {
  border-radius: 1.5rem;
  font-family: "Avontuurgardegoshikkutai-5Doa";
  font-display: swap;
  margin-top: 15px;
  /* height: 300px;
  overflow-y: scroll;
  overflow-x: hidden; */
  max-width: 500px;
  background-color: rgba(255, 255, 255, .7);
  text-align: center;
  font-size: 16.5px;
  border: 3px solid #9a16e3;
}

.manifesto-container::-webkit-scrollbar {
  width: 12px;
  border-radius: 1rem;
  /* width of the entire scrollbar */
}

.manifesto-container::-webkit-scrollbar-track {
  background: grey;
  /* color of the tracking area */
  border-radius: 1rem;
}

.manifesto-container::-webkit-scrollbar-thumb {
  background-color: #9a16e3;
  /* color of the scroll thumb */
  border-radius: 1rem;
  /*border: 3px solid orange;  /* creates padding around scroll thumb */
}

.manifesto-carousel-img {
  width: 10% !important;
  border-radius: 1.5rem;
  margin: 5px;
}

